* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  background-color: rgba(0, 128, 255, 0.197);
}
.header {
  display: flex;
  justify-content: space-between;
  border: 1px solid black;
  .link {
    padding: 0.5vh;
    text-decoration: none;
    color: #d7a202;
    font-size: 15px;
    background-color: white;
    text-align: center;
    border-left: 1px solid black;
    border-right: 1px solid black;
    transition: all 0.1s;
  }
  .link:hover {
    color: black;
  }
  .link.active {
    color: black;
    background-color: rgba(0, 128, 255, 0.197);
  }
}
.page {
  margin: 5vh 3vw;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .inputs {
    display: flex;
    flex-wrap: wrap;
    div {
      flex-direction: column;
      flex-wrap: wrap;
      input {
        width: max-content;
      }
    }
  }

  .result {
    width: 30vw;
    height: 33vh;
    padding: 12px 20px;
    box-sizing: border-box;
    border: 2px solid #ccc;
    border-radius: 10px;
    background-color: #f8f8f8;
    resize: none;
  }
}

.table {
  margin: 1vh 1vw;
  padding: 0.5vh 0.5vw;
  border: 1px solid black;
  border-radius: 5px;
  tr {
    margin: 0.2vh 0.2vw;
    td {
      margin: 0.2vh 0.2vw;
    }
  }
}
input,
button {
  margin: 0.2vh 0.2vw;
  width: 7vw;
  height: max-content;
  border: 1px solid black;
  border-radius: 5px;
  padding: 0.5vh 0.5vw;
  cursor: pointer;
  transition: all 0.2s;
}
button:active {
  background-color: rgba(0, 0, 0, 0.589);
  color: rgb(255, 255, 255);
}
button {
  margin: 1vh 1vw;
  width: 10vw;
}
